import ApiService from "@/core/services/ApiService";
import IMessage from "@/store/modules/message/interface/message.interface"; 
import JwtService from "@/core/services/JwtService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class MessageService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/message`, httpOptions());
  }

  edit(id: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/message/${id}`, httpOptions());
  }

  create(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/message`, data, httpOptions());
  }

  update(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/message/${id}`, data, httpOptions());
  }

  updateOrder(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/message/update-order`, data, httpOptions());
  }

  destroy(id:any) {
    return axios.delete(`${ApiService.vueInstance.axios.defaults.baseURL}/message/${id}`, httpOptions());
  }

  changeStatus(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/message/${id}/status`, data, httpOptions());
  }
}
