
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import MessageService from "@/store/modules/message/service/message.service";
import IMessage from "@/store/modules/message/interface/message.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import draggable from 'vuedraggable'
import { createToaster } from "@meforma/vue-toaster";

export default defineComponent({
	name: "Messages",
	components: {
		draggable
	},
	setup() {
		const router = useRouter();
		const messageService = new MessageService();
		const messages = ref<Array<any>>([]);
		const show = ref<Boolean>(false);
		const toaster = createToaster({ position: "bottom-left" });

		const getData = ()=>{
			messages.value = []
			messageService.getData()
			.then((response) => {
				response.data.forEach((element: any) => {
					element.visible = (element.visible == 1)
				});
				messages.value = response.data
			})
			.catch((error) => {
				
			});
		}

		const saveStatus = (message: any) => messageService.changeStatus(message.id, message)
        .then((response) => {
            Swal.fire(
            'Confirmación!',
            'Información actualizada',
            'success'
            )
        })
        .catch((error) => {
            Swal.fire(
            'Oh no!',
            'No se guardaron los cambios',
            'error'
            )
        });

		const changeValue = (e: any, message: any) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar el estado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						saveStatus(message);
					}
					else{
						messages.value.forEach((element: any, index: any) => {
							if(element.id == message.id){
								messages.value[index].visible = !message.visible;
							}
						});
					}
			})
		}

		const deleteData = (messageId) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea eliminar el registro seleccionado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Eliminar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						messageService.destroy(messageId)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información eliminada exitosamente',
							'success'
							).then(result => {
								getData()
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se completo la operación',
							'error'
							);
						});
					}
			})
			
		}

		const changeOrder = ()=>{
			messages.value.forEach((message: any, index: any) => {
				messages.value[index].order = index + 1
			})
			saveOrder(getIdsOrder())
		}

		const getIdsOrder = ()=>{
			let idsOrder: any[] = []
			messages.value.forEach((message: any, index: any) => {
				idsOrder.push({id: message.id, order: message.order})
			})
			return idsOrder
		}

		const saveOrder = (idsOrder)=>{
			messageService.updateOrder({data: idsOrder})
			.then((response) => {
				toaster.success(`Orden actualizo`);
			})
			.catch((error) => {
				toaster.error(`No se guardaron los cambios`);
			});
		}
		
		onMounted(() => {
			getData()
		});

		return {
			messages,
			changeValue,
			deleteData,
			changeOrder
		};
	},
});
